import React from "react";

const NewsItem=(props)=>{


    let {title,description,urlImage,newsUrl,author,publishOn}=props;

    return (
      <div>
      <div className="container">
        <div className="card my-3" style={{width:'18rem'}}>
          <img src={!urlImage?"https://images.moneycontrol.com/static-mcnews/2017/03/IPO_initial_public_offerings_IPO_1280x720-770x433.jpg":urlImage} className="card-img-top" alt="..." />
          <div className="card-body">
            <h5 className="card-title">{title}</h5>
            <p className="card-text">
             {description}
            </p>
            <p className="Card-text"> Published BY-{author}</p>
            <p className="Card-text">on-{publishOn}</p>
            <a href={newsUrl} className="btn  btn-sm btn-success">
              Read more
            </a>
          </div>
        </div>
      </div>
     </div>
    );
  
}

export default NewsItem;
