import React, { Component } from 'react'
import NewsItem from "./NewsItem"
import Logo from "./Logo";
import PropTypes from 'prop-types'


export class News extends Component {
         
  static defaultProps={
      country:'in',
      pageSize:8,
      category:'general',
  }

  static propTypes={
    country:PropTypes.string,
    pageSize:PropTypes.number,
    category:PropTypes.string,
}



   constructor(props)
    {
        super(props);
        console.log('vinod');
        this.state={
            articles: [],
            loading: false,
            page:1 
      }
      document.title=`${this.props.category} -DailyNews`;
    }

    async componentDidMount()
    {
      console.log("cdm");
      let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=a1c148281f14462a9e84b736585aa430&page=1&pageSize=${this.props.pageSize}`;
      this.setState({loading:true});
      let data=await fetch(url);
      let parseData=await data.json()
      console.log(parseData);
     this.setState({articles:parseData.articles,totalResults:parseData.totalResults,
    loading:false})
    }
  
    handleOnPrev=async()=>{
       
        let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=a1c148281f14462a9e84b736585aa430&page=${this.state.page-1}&pageSize=${this.props.pageSize}`;  
        this.setState({loading:true});
        let data=await fetch(url);
        let parseData=await data.json()
        console.log(parseData);
        this.setState({
          page:this.state.page-1,
          articles:parseData.articles,
          loading:false})

    } 

 
    handleNextClick=async()=>{
        console.log("Manoj");
         if(!(this.state.page+1>Math.ceil(this.state.totalResults/this.props.pageSize)))
       {
        let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=a1c148281f14462a9e84b736585aa430&page=${this.state.page+1}&pageSize=${this.props.pageSize}`;  
        this.setState({loading:true});
        let data=await fetch(url);
        let parseData=await data.json()
        console.log(parseData);
        this.setState({
          page:this.state.page+1,
          articles:parseData.articles, 
          loading:false})
      }
    }
      



    render() {
        return (
            <div className="container my-5">
                  
                 <h1 className="text-center" style={{marginTop:'70px'}}>Daily News- {this.props.category}</h1>
                 <div className="text-center">{this.state.loading && <Logo />}</div>
                 <div className="row">
                {this.state.articles.map((element)=>{
                   return <div className="col-md-4" key={element.url}>
                             <NewsItem 
                             title={element.title} 
                             description={element.description} 
                             urlImage={element.urlToImage}
                             newsUrl={element.url}
                             author={element.author}
                             publishOn={element.publishedAt}
                              />
                        </div>
            })}
                  
                 </div>
                <div className="container d-flex justify-content-between">
                    <button disabled={this.state.page<=1} type="button" onClick={this.handleOnPrev} className="btn btn-success"> &larr; Prev</button>
                    <button  disabled={this.state.page+1>Math.ceil(this.state.totalResults/20)} type="button" onClick={this.handleNextClick} className="btn btn-success">Next  &rarr;</button>
                </div>
            </div>
        )
    }
}

export default News








